<template>
  <div class="result-v" id="view">
    <img class="view-bg" src="../assets/info-bg.png" alt="">

    <div class="view__wrapper">
      
      <div class="video__wrapper" id="video__wrapper" v-show="fileName && !videoFinished">
        <video v-show="videoExist" id="vid" :src="`http://dedmoroz-rt.com/out/${fileName}`" poster="../assets/poster.jpg"></video>
        <div v-show="videoExist" class="play" @click="play()" id="play-btn">
          <img src="../assets/play.png" alt="">
        </div>  

        <div class="title__wrapper" v-show="!videoExist">
          <div class="title__white">Коллега отправил тебе персональную видео-открытку. Она будет готова через пару минут.</div>
          <div class="title__yellow">Дождись, оно того стоит!</div>
          <h1>&#128521;</h1>
        </div>

      </div>

      <h1 v-if="videoFinished">C Новыг годом!</h1>

      <div class="video__another" v-if="videoFinished">
        <button type="submit" onclick="window.location.href='http://dedmoroz-rt.com/'"><span>Cоздать видео-поздравление для коллеги</span></button>
      </div>
    </div>
    

  </div>
</template>

<script>
export default {
  name: 'VideoView',
  data() {
    return {
      fileName: undefined,
      videoFinished: false,
      videoExist: false
    }
  },
  mounted(){
    console.log('hi');
    window.loadedData = false;

    document.getElementById('vid').addEventListener('loadeddata', () => {

      if(!window.loadedData){
        console.log('loaded');
        window.loadedData = true;
        this.videoExist = true;
      }
      
    });
    document.getElementById('vid').addEventListener('error', () => {
      console.log('error');

      setTimeout(() => {

        this.videoExist = false;
        
        const queryString = window.location.search;
        const urlParams = new URLSearchParams(queryString);
        const video = urlParams.get('video')
        
        console.log(video);

        this.fileName = '';
        this.fileName = video;

        document.getElementById('vid').load();

        // http://dedmoroz-rt.com/out/
      }, 10000);
    });

    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const video = urlParams.get('video')
    console.log(video);
    this.fileName = video;

    

  },
  methods: {
    play(){
      document.getElementById('vid').play();
      document.getElementById('video__wrapper').classList.add('no-after');
      document.querySelector('#play-btn').style.display = 'none'; 
      // document.getElementById('pause').style.display = 'flex';
      document.getElementById('vid').addEventListener('ended', () => {
        this.videoFinished = true;
      });
    }
  }
}
</script>

